import React, { Component } from 'react'
import "./RegisterDropdown.css";

export default class RegisterDropdown extends Component {
    render() {
        return (
            <div className="rDropdown_con">
                <select className="rDropdown_select" onChange={this.props.onChange}>
                    {
                        this.props.options.map((v,i)=>(
                            <option key={`${i}`} className="rDropdown_options" value={v.value}
                            dangerouslySetInnerHTML={{__html:v.title}}></option>
                        ))
                    }
                </select>
                <div className="rDropdown_title">{this.props.title}</div>
            </div>
        )
    }
}
