import React, { Component } from 'react';
import RegisterDropdown from '../components/RegisterDropdown';
import RegisterInput from '../components/RegisterInput';
import login_left_arrow from '../assets/images/login_left_arrow.png';
import "./Login.css";
import logo from '../assets/images/logo.png';

export default class Login extends Component {

    state = {
        mobile_valid:false
    }

    // goToRegister= ()=>{
    //     window.location.href = "/register"
    // }

    onLoginWithMobileConnect = ()=>{
        window.location.href = "/dashboard/banktransfer"
    }

    render() {
        return (
            <div className="login_con">
                <div className="card_con">

                    <img className="login_logo" src={logo}/>

                    <div className="login_card_title">{"ورود"}</div>

                    <RegisterInput title="نام کاربری" valid={this.state.mobile_valid}/>

                    <RegisterInput title="رمز عبور" valid={this.state.mobile_valid}/>
                    
                    {/* <RegisterInput title="شماره موبایل" valid={this.state.mobile_valid}/> */}

                    {/* <RegisterDropdown title={"انتخاب شیوه احراز هویت"} options={auth_options}/> */}

                    <div className="login_btn">{"ورود"}</div>

                    <div className="login_btn" style={{backgroundColor:"#30447C"}}
                    onClick={this.onLoginWithMobileConnect}>{"ورود با موبایل کانکت"}</div>

                    {/* <div className="login_go_signup">{"حساب کاربری ندارم."}
                        <span onClick={this.goToRegister}>{"ساخت حساب کاربری جدید"}</span>
                        <img onClick={this.goToRegister} className="login_left_arrow" src={login_left_arrow}/>
                    </div> */}

                </div>
            </div>
        )
    }
}

const auth_options = [
    {title:"موبایل کانتکت", value:"mobile_contact"},
    {title:"اس ام اس لینک", value:"sms_link"},
    {title:"اپلیکیشن", value:"application"},
]
