import React, { Component } from 'react';
import './BankTransferSuccess.css';
import success from '../assets/images/success.png';

export default class BankTransferSuccess extends Component {
    render() {
        return (
            <div id="bts_con">

                <div id="bts_card_con">

                    <img id="bts_success_img" src={success}/>

                    <div id="bts_title">{"انتقال وجه با موفقیت انجام شد."}</div>

                    <div id="bts_text1">{"کد پیگیری تراکنش :"}</div>

                    <div id="bts_code">{"123785648155597693"}</div>

                    <div id="bts_btn">{"بازگشت به خانه"}</div>

                </div>
                
            </div>
        )
    }
}
