import React, { Component } from 'react'
import Controller from '../Controller';
import { setCookie } from '../helpers/cookie';

export default class SaveToken extends Component {

    componentDidMount(){
        var url_string = window.location.href;
        var url = new URL(url_string);
        var cookie = url.searchParams.get("cookie");
        
        if(cookie){
            
            // set the cookie in browser
            setCookie("JSESSIONID", cookie, 365);
            
            // redirect to /dashboard/banktransfer
            window.location.href = "/dashboard/banktransfer";

        }else{
            alert("no cookie param passed to the url");
        }
    }
    
    render() {
        return (
            <div>
                
            </div>
        )
    }
}

