import React, { Component } from 'react';
import './BankTransferFail.css';
import fail from '../assets/images/fail.png';

export default class BankTransferFail extends Component {
    render() {
        return (
            <div id="btf_con">

                <div id="btf_card_con">

                    <img id="btf_success_img" src={fail}/>

                    <div id="btf_title">{"انتقال وجه با خطا مواجه شد."}</div>

                    <div id="btf_text1">{"خطای رخ داده :"}</div>

                    <div id="btf_error">{"به فلان دلیل خطا رخ داده است."}</div>

                    <div id="btf_btn">{"انتقال مجدد"}</div>

                </div>
                
            </div>
        )
    }
}
