import React, { Component } from 'react';
import './Register.css';
import login_left_arrow from '../assets/images/login_left_arrow.png';
import RegisterInput from '../components/RegisterInput';
import {Button, Checkbox} from 'antd';
import arrow_white from '../assets/images/arrow_white.png';

export default class Register extends Component {

    state={
        phone_valid:false,
    }

    goToLogin=()=>{
        window.location.href="/login";
    }

    render() {
        return (
            <div className="register_con">

                <div className="register_card_con">
                    
                    <div className="register_logo">{"لوگو"}</div>

                    <div className="register_sec">

                        <div className="register_title_sec">
                            <div className="register_title">{"ثبت نام"}</div>
                            <div className="register_go_login" onClick={this.goToLogin}>{"حساب کاربری دارم"}
                                <img className="register_go_login_arrow" src={login_left_arrow}/>
                            </div>
                        </div>

                        <RegisterInput title={"شماره موبایل"} valid={this.state.phone_valid}/>
                        <RegisterInput title={"نام"} valid={this.state.phone_valid}/>
                        <RegisterInput title={"نام خانوادگی"} valid={this.state.phone_valid}/>
                        <RegisterInput title={"کد ملی"} valid={this.state.phone_valid}/>
                        <RegisterInput title={"ایمیل"} valid={this.state.phone_valid}/>
                        <RegisterInput title={"رمز عبور"} valid={this.state.phone_valid}/>
                        <RegisterInput title={"تکرار رمز عبور"} valid={this.state.phone_valid}/>

                        <div className="register_rules_sec">
                            <Checkbox className="register_rules_checkbox"/>
                            <span className="register_rules">{" قوانین و مقررات "}</span>
                            <span>{"را با مطالعه کردم و می پذیرم."}</span>
                        </div>

                        <div className="register_btn">
                            {"ثبت نام"}
                            <img className="register_btn_icon" src={arrow_white}/>
                        </div>

                        <div className="register_newnumber_con">
                            {"قبلا ثبت نام کردم."}
                            <span className="register_newnumber_link">{"ثبت نام با شماره موبایل جدید"}</span>
                            <img className="register_newnumber_arrow" src={login_left_arrow}/>
                        </div>

                    </div>

                    <div className="register_slide_sec">

                    </div>

                </div>
            </div>
        )
    }
}
