import React, { Component } from 'react'
import "./RegisterInput.css";
import valid_img from "../assets/images/valid.png";
import {priceNum} from '../helpers/persianNum';

export default class RegisterInput extends Component {

    state={input:""}
    
    onChange=(e)=>{
        let val = e.target.value;

        if(this.props.data_type==="number" || this.props.data_type==="price"){

            if(isNaN(Number(val[val.toString().length-1])) || val.toString().length>15){
                e.target.value = this.state.input;
            }

            if(val == 0){e.target.value=0}
        }

        this.props.onChange(e.target.value);

        this.setState({input:e.target.value});
    }

    render() {

        let status_img = null;
        if(this.props.valid){
            status_img = valid_img;
        }

        return (
            <div className="rinput_con">

                <input className="rinput" onChange={this.onChange} value={this.state.input}
                placeholder={this.props.title}/>

                <img className="rinput_icon" src={status_img}/>

                {
                    this.state.input.length?
                    <div className="rinput_title">{this.props.title}</div>:null
                }
                
                {
                    this.props.unit?
                    <div className="rinput_unit">{this.props.unit}</div>:null
                }
                
            </div>
        )
    }
}
