import React, { Component } from 'react';
import "./DashboardMenu.css";
import logo from '../assets/images/logo.png';
import no_pic from '../assets/images/pers_no_img.jpg';
import user from '../assets/images/user.png';
import user_on from '../assets/images/user_on.png';
import home from '../assets/images/home.png';
import home_on from '../assets/images/home_on.png';
import security from '../assets/images/security.png';
import security_on from '../assets/images/security_on.png';
import cellphone from '../assets/images/cellphone.png';
import cellphone_on from '../assets/images/cellphone_on.png';
import headphone from '../assets/images/headphones.png';
import headphone_on from '../assets/images/headphones_on.png';
import Controller from '../Controller';
import Server from '../Server';
import { getCookie } from '../helpers/cookie';

export default class DashboardMenu extends Component {

    constructor(props){
        super(props);
        let active = "none";
        switch(window.location.pathname){
            case"/dashboard/home":active="dashboard";break;
            case"/dashboard/profile":active="profile";break;
            case"/dashboard/security":active="security";break;
            case"/dashboard/applet":active="applet";break;
            case"/dashboard/contact":active="contact";break;
        }
        this.state={
            active,
            firstname:"",
            lastname:"",
        }
        if(Controller.user_firstname){
            this.state.firstname=Controller.user_firstname;
            this.state.lastname=Controller.user_lastname;
        }
    }

    componentDidMount(){
        if(!Controller.user_firstname){
            
            Server.get_request("/api/user", (res)=>{

                if(res.fname){
                    Controller.user_firstname = res.fname;
                    Controller.user_lastname = res.lname;
                }
                this.setState({firstname:Controller.user_firstname,
                lastname:Controller.user_lastname});

            }, {JSESSIONID:getCookie("JSESSIONID")});
        }
    }

    onSelect=(name)=>{
        window.location.pathname="/dashboard/"+name;
    }

    render() {
        return (
            <div id="dashMenu_con">

                <div id="dashMenu_wrapper">

                    <img id="dashMenu_logo" src={logo}/>

                    <div id="dashMenu_sec1">
                        <img id="dashMenu_profilepic" src={no_pic}/>
                        <div id="dashMenu_username">{this.state.firstname+" "+this.state.lastname}</div>
                    </div>

                    <DashMenuBtn title="داشبورد" icon={home} active_icon={home_on} name="dashboard" 
                    onSelect={this.onSelect} active={this.state.active}/>
                    <DashMenuBtn title="پروفایل کاربری" icon={user} active_icon={user_on} name="profile"
                    onSelect={this.onSelect} active={this.state.active}/>
                    <DashMenuBtn title="امنیت" icon={security} active_icon={security_on} name="security"
                    onSelect={this.onSelect} active={this.state.active}/>
                    <DashMenuBtn title="اپلت" icon={cellphone} active_icon={cellphone_on} name="applet"
                    onSelect={this.onSelect} active={this.state.active}/>
                    <DashMenuBtn title="پشتیبانی" icon={headphone} active_icon={headphone_on} name="contact"
                    onSelect={this.onSelect} active={this.state.active}/>

                </div>
                
            </div>
        )
    }
}


function DashMenuBtn(props){

    if(props.active === props.name){
        return(
            <div className="dashbtn_con" style={{backgroundColor:"rgba(153, 178, 255, 0.2)"}}>
                <img className="dashbtn_icon" src={props.active_icon}/>
                <div className="dashbtn_title" style={{color:"#5064a0"}}>{props.title}</div>
            </div>
        )
    }

    return(
        <div className="dashbtn_con" onClick={()=>{props.onSelect(props.name)}}>
            <img className="dashbtn_icon" src={props.icon}/>
            <div className="dashbtn_title">{props.title}</div>
        </div>
    )
}