import React, { Component } from 'react';
import RegisterDropdown from '../components/RegisterDropdown';
import RegisterInput from '../components/RegisterInput';
import "./BankDepositTransfer.css";
import sync from '../assets/images/sync_icon.png';
import persianJs from 'persianjs';
import Server from '../Server';
import Controller from '../Controller';
import { getCookie } from '../helpers/cookie';


export default class BankDepositTransfer extends Component {

    state={
        transaction_loading:false,
        pending_transaction_id:null,
        price_fa:"",
        source:"",
        destination:"",
        amount:"",
        method:"1",
        description:"",
    }

    onTransfer = ()=>{

        let json = {
            source:this.state.source,
            destination:this.state.destination,
            amount:this.state.amount,
            method:this.state.method,
            description:this.state.description,
        }

        if(data_is_valid(json)){

            Server.post_request(Server.urls.BANK_TRANSFER, json, (res)=>{

                if(res.isSuccessfull){

                    this.checkTransactionResult();
                    this.setState({transaction_loading:true, pending_transaction_id:res.id});

                }else{

                    Controller.openNotification(res.error, null, "error");
                }

            }, {
                "JSESSIONID": getCookie("JSESSIONID")
            });
        }
    }

    checkTransactionResult = ()=>{
        setTimeout(()=>{
            Server.get_request("/api/status/"+this.state.pending_transaction_id, (res)=>{

                if(res.status === "confirmed"){

                    window.location.href = "/dashboard/banktransfersuccess";

                }else if(res.status === "pending"){
                    
                    this.checkTransactionResult();

                }else{

                    Controller.transaction_status_error = res.status;
                    window.location.href = "/dashboard/banktransferfail";
                }

            }, {"JSESSIONID": getCookie("JSESSIONID")})
        }, 2000);
    }

    onCancel = ()=>{
        Controller.openNotification("سلام", null, "success")
    }

    onPriceChange=(val)=>{
        let temp = (val/10).toFixed(0);
        temp = persianJs(temp).digitsToWords();
        this.setState({price_fa:temp+" تومان", amount:val});
    }

    onSource = (val)=>{
        this.state.source = val;
    }

    onDestination = (val)=>{
        this.state.destination = val;
    }

    onMethod = (e)=>{
        this.state.method = e.target.value;
    }

    onDescription = (e)=>{
        this.setState({description:e.target.value});
    }

    render() {
        return (
            <div id="bdt_con">

                {
                    this.state.transaction_loading?
                    <div id="bdt_loading_blur">
                        <div id="bdt_loading_blur_warrper">
                            <img id="bdt_sync_icon" src={sync}/>
                            <div id="bdt_sync_text">{"درحال انجام تراکنش"}</div>
                        </div>
                    </div>:null
                }

                <div id="bdt_title">{"انتقال سپرده"}</div>
                <div id="bdt_subtitle">در این بخش انتقال سپرده صورت می گیرد و...</div>

                <div id="bdt_card_con">

                    <div id="bdt_card_sec1">

                        <div id="bdt_card_sec3">

                            <div id="bdt_alertcon">{"توجه : "}
                                <p>{"از تاریخ ..."}</p>
                            </div>

                        </div>

                        <div id="bdt_card_sec4">

                            <RegisterInput title="حساب مبداء" data_type="creditcard" onChange={this.onSource}/>
                            <RegisterInput title="حساب مقصد" data_type="creditcard" onChange={this.onDestination}/>

                            {/* <RegisterDropdown title="حساب مبداء" options={temp1_options} /> */}
                            
                            {/* <RegisterDropdown title="حساب مقصد" options={temp1_options} /> */}
                            
                            <RegisterInput title="مبلغ انتقال" data_type="price" onChange={this.onPriceChange} 
                            unit="ریال"/>
                            
                            <div id="bdt_card_fa_price">{this.state.price_fa}</div>
                            
                            <RegisterDropdown title="انتخاب شیوه احرازهویت" options={auth_options} onChange={this.onMethod}/>
                            
                            <textarea id="bdt_card_desc"  placeholder="توضیحات" onChange={this.onDescription}>
                            </textarea>

                        </div>

                    </div>

                    <div id="bdt_card_sec2">
                        <div id="bdt_btn_accept" onClick={this.onTransfer}>{"تایید و ادامه"}</div>
                        <div id="bdt_btn_cancel" onClick={this.onCancel}>{"انصراف"}</div>
                    </div>

                </div>
                
            </div>
        )
    }
}

const temp1_options = [
    {title:`2347-5546-3660-4240    قرض الحسنه`, value:'1'},
    {title:'2347-5546-3660-4240    قرض الحسنه', value:'2'},
    {title:'2347-5546-3660-4240    قرض الحسنه', value:'3'},
    {title:'2347-5546-3660-4240    قرض الحسنه', value:'4'},
]

const auth_options = [
    {title:"موبایل کانتکت", value:"1"},
    {title:"اس ام اس لینک", value:"2"},
    {title:"اپلیکیشن", value:"3"},
]


function data_is_valid(json){

    let valid = true;

    if(isNaN(json.source) || json.source.length<10 || json.source.length>20){
        valid = false;
        Controller.openNotification("شماره مبداء نامعتبر وارد شده است", null, "error")
    }

    if(isNaN(json.destination) || json.destination.length<10 || json.destination.length>20){
        valid = false;
        Controller.openNotification("شماره مقصد نامعتبر وارد شده است", null, "error")
    }

    if(isNaN(json.amount) || !json.amount){
        valid = false;
        Controller.openNotification("مبلغ  انتقال نامعتبر می باشد", null, "error")
    }

    return valid;
}
