import React, { Component } from 'react';
import './App.css';
import 'antd/dist/antd.css';
import {BrowserRouter, Route} from "react-router-dom"
import Login from './pages/Login';
import Root from './pages/Root';
import Register from './pages/Register';
import DashboardMenu from './components/DashboardMenu';
import BankDepositTransfer from './pages/BankDepositTransfer';
import BankTransferSuccess from './pages/BankTransferSuccess';
import BankTransferFail from './pages/BankTransferFail';
import SaveToken from './pages/SaveToken';
import Controller from './Controller';
import { notification } from 'antd';
import { CheckCircleOutlined, ExclamationCircleOutlined } from '@ant-design/icons';
import Sercurity from './pages/Sercurity';

class App extends Component {

  componentDidMount(){
    Controller.openNotification = openNotification;
  }

  render(){
    return (
      <div className="App">
        <BrowserRouter>
          <Route exact path="/" component={Root}/>
          <Route exact path="/login" component={Login}/>
          {/* <Route exact path="/register" component={Register}/> */}
          <Route path="/dashboard" component={DashboardMenu}/>
          <Route path="/dashboard/banktransfer" component={BankDepositTransfer}/>
          <Route path="/dashboard/banktransfersuccess" component={BankTransferSuccess}/>
          <Route path="/dashboard/banktransferfail" component={BankTransferFail}/>
          <Route path="/dashboard/security" component={Sercurity}/>
          <Route exact path="/handler/savetoken" component={SaveToken}/>
        </BrowserRouter>
      </div>
    );
  }
}

export default App;

const openNotification = (title, description, icon) => {
  if(icon=="error"){icon=<ExclamationCircleOutlined style={{ color: '#ff0800' }}/>}
  if(icon=="success"){icon=<CheckCircleOutlined style={{ color: '#00c957' }}/>}
  if(icon=="alert"){icon=<ExclamationCircleOutlined style={{ color: '#ffbb00' }}/>}
  notification.open({
    message: title,
    duration:5,
    description,
    icon,
    placement:"topLeft"
  });
};
